function getFileListFromEvent(event) {
    if ('clipboardData' in event && 'files' in event.clipboardData) {
        var files = event.clipboardData.files;
        if (files) {
            return files;
        }
        else {
            throw new TypeError('TypeError');
        }
    }
    if ('dataTransfer' in event) {
        var files = event.dataTransfer.files;
        if (files) {
            return files;
        }
        else {
            throw new TypeError('TypeError');
        }
    }
    if (event.target) {
        var files = event.target.files;
        if (files) {
            return files;
        }
        else {
            throw new TypeError('TypeError');
        }
    }
    throw new TypeError('TypeError');
}
export function createDataURIImage(event) {
    return new Promise(function (resolve, reject) {
        try {
            var files = getFileListFromEvent(event);
            var target = files[0];
            var reader = new FileReader();
            reader.onload = function (event) {
                var result = event.currentTarget.result;
                resolve(result);
            };
            reader.readAsDataURL(target);
        }
        catch (e) {
            reject(e);
        }
    });
}
export function createBlogImageFromDataURI(dataURI) {
    var byteString = atob(dataURI.split(',')[1]);
    var type = dataURI
        .split(',')[0]
        .split(':')[1]
        .split(';')[0];
    var ab = new ArrayBuffer(byteString.length);
    var ia = new Uint8Array(ab);
    for (var i = 0; i < byteString.length; i++) {
        ia[i] = byteString.charCodeAt(i);
    }
    return new Blob([ab], { type: type });
}
