import Vue from 'vue';
import ToolbarIcon from './ToolbarIcon.vue';
import { BlockType } from '../../types/Blocks';
function getTargetTag() {
    if (navigator.userAgent.includes('Edge'))
        return 'body';
    if (!window.chrome && 'WebkitAppearance' in document.documentElement.style)
        return 'body';
    return 'html';
}
export default Vue.extend({
    props: {
        isSaving: Boolean,
        hasactive: Boolean,
        activeRoot: {
            type: Object,
            required: false,
            default: {}
        }
    },
    components: {
        ToolbarIcon: ToolbarIcon
    },
    data: function () {
        return {
            BlockType: BlockType,
            isFixed: true,
            isOpen: false,
            beforeScroll: 0
        };
    },
    mounted: function () {
        // const $ = (e: string) => document.querySelector(e) as any
        // const tag = getTargetTag() as any
        // window.addEventListener('scroll', () => {
        //   this.isFixed = $(tag)!.scrollTop > this.beforeScroll
        //   this.beforeScroll = $(tag)!.scrollTop
        // })
    },
    methods: {
        toggleIsOpen: function () {
            this.isOpen = !this.isOpen;
        },
        dispatchUpload: function () {
            ;
            this.$el.querySelector('[type="file"]').click();
        },
        append: function (type) {
            var _this = this;
            setTimeout(function () {
                _this.isOpen = false;
                _this.$emit('append', type);
                _this.$emit('disable');
            }, 100);
        },
        appendParagraph: function () {
            this.isOpen = false;
            this.$emit('append', BlockType.Paragraph);
            this.$emit('disable');
        },
        appendRule: function () {
            this.append(BlockType.Rule);
        },
        appendQuote: function () {
            this.isOpen = false;
            this.$emit('append', BlockType.Quote);
            this.$emit('disable');
        },
        appendHeading: function () {
            this.isOpen = false;
            this.$emit('append', BlockType.Heading);
            this.$emit('disable');
        },
        handleUpload: function (event) {
            this.isOpen = false;
            this.$emit('disable');
            this.$emit('upload', event);
        },
        handleClickPublish: function () {
            this.$emit('publish');
        }
    }
});
