import Vue from 'vue';
import ShadowInput from '../ShadowInput.vue';
export default Vue.extend({
    components: {
        ShadowInput: ShadowInput
    },
    props: {
        block: Object
    },
    methods: {
        handleDelete: function () {
            this.$emit('delete', this.block);
        },
        handleAddImage: function (src) {
            this.$emit('addimageuri', src);
        }
    }
});
